import { PAGE_SIZE } from '@/assets/config'
import { CODE, scoreKeys } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'score',
  data () {
    return {
      code: CODE.Score,
      dataKeys: scoreKeys,
      items: [],
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      operation: {},
      service: Service,
      condition: {},
      searchOptions: [
        { label: '发表时间', key: 'publishing_time', type: 'date' },
        { label: '姓名', key: 'name' },
        //{ label: '作者', key: 'first_author' }
      ]
    }
  },
  methods: {
    publicSearch (condition) {
      for (const key in condition) {
        this.condition[key] = condition[key]
      }
      this.getDataTables()
    },
    search () {
      this.getDataTables()
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    getDataTables (page = 1) {
      const request = {

      }
      if (page) {
        request['page'] = page
      }
      for (const key in this.condition) {
        if (this.condition[key]) {
          request[key] = this.condition[key]
        }
      }

      Service.Score.get(request).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data
          this.total = response.data.total
          setTimeout(() => utilizeService.iCheck(), 200)
        }
      })
    },
    download (item) {
      this.fileExport({
        ids: [item.id],
        model: this.code
      })
    },
    fileExport (request) {
      Service.FileDownload(request).then(response => {
        dataService.download(response)
      })
    },
    delConfirm (item) {
      Service.Score.del(item.id).then(response => {
        if (response.code == 200) {
          this.getDataTables()
        }
      })
    },
    add () {
      tabsService.addDetailsTab('/views/score/new', '评分-新增')
    },
    edit (item) {
      tabsService.addDetailsTab('/views/score/' + item.id, '评分-详情-' + item.id)
    }
  },
  created () {
    if (Cache.hasPermission('score')) {
      this.operation.add = true
      this.operation.del = true
      this.operation.edit = true
      this.operation.export = true
    }
    if (Cache.hasPermission('content_management')) {
      this.operation.approval = true // 审批权限
    }
    this.getDataTables()
  },
  mounted () {
  }
}



